.social-media {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-gradient(linear, right top, left top, from(#64499b), to(#00b2dc));
    background: -webkit-linear-gradient(right, #64499b, #00b2dc);
    background: -o-linear-gradient(right, #64499b, #00b2dc);
    background: linear-gradient(to left, #64499b, #00b2dc);
}

.social-media__wrapper {
    padding: 80px 40px;
}

.social-media__header {
    font-size: 32px;
    color: #fff;
}

.social-media__left-block {
    display: -ms-flexbox;
    display: flex;
    margin-right: 30px;
}

.social-media__right-block {
    display: -ms-flexbox;
    display: flex;
}

.social-media__icons {
    display: -ms-flexbox;
    display: flex;
    margin-top: 10px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.social-media__icons a {
    padding-right: 30px;
    margin-top: 20px;
}

.social-media__icons a:last-of-type {
    padding-right: 0;
}

.social-media__icon {
    width: 59px;
    height: 59px;
}

.social-media__btc {
    background: url('../../images/social-media/bitcoin-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__youtube {
    background: url('../../images/social-media/youtube-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__instagram {
    background: url('../../images/social-media/instagram-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__reddit {
    background: url('../../images/social-media/reddit-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__tel {
    background: url('../../images/social-media/telegram-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__twitter {
    background: url('../../images/social-media/twitter-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__github {
    background: url('../../images/social-media/github-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__facebook {
    background: url('../../images/social-media/facebook-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__medium {
    background: url('../../images/social-media/medium-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__linkedin {
    background: url('../../images/social-media/linkedin-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__wechat {
    background: url('../../images/social-media/wechat-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__kakaotalk {
    background: url('../../images/social-media/kakaotalk-icon.svg') no-repeat 0 0;
    background-size: cover;
}

.social-media__weibo {
    background: url('../../images/social-media/weibo-icon.svg') no-repeat 0 0;
    background-size: cover;
}

@media screen and (max-width: 1439px) {
    .social-media__wrapper {
        width: 90%;
    }
}

@media screen and (max-width: 1023px) {
    .social-media__header {
        word-break: break-word;
        width: 70%;
    }
}

@media screen and (max-width: 767px) {
    .social-media {
        display: none;
    }
}


