.button {
  width: 100%;
  font-family: Geometria;
  font-weight: 600;
  border-radius: 30px;
  border: 1px solid #fff;
  text-align: center;
  color: #fff;
  cursor: pointer;
  padding: 10px 25px;
}

.button:hover {
  background-color: #fff;
  color: #6e5fd6;
}

.button_gradient {
  border: none;
  background-image: -webkit-gradient(linear, right top, left top, from(#6e5fd6), to(#f086b8));
  background-image: -webkit-linear-gradient(right, #6e5fd6, #f086b8);
  background-image: -o-linear-gradient(right, #6e5fd6, #f086b8);
  background-image: linear-gradient(to left, #6e5fd6, #f086b8);
  -webkit-box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
  
}

.button_gradient:hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#6e5fd6), to(#f086b8));
  background-image: -webkit-linear-gradient(left, #6e5fd6, #f086b8);
  background-image: -o-linear-gradient(left, #6e5fd6, #f086b8);
  background-image: linear-gradient(to right, #6e5fd6, #f086b8);
  color: #fff;
}



